import React, { Component } from 'react';
import DrumMachine from './DrumMachine';
import Bubbles from './Bubbles';
import PerDialogue from './PerDialogue';
import cheer from '../sounds/cheer.wav';
import SongOptions from './SongOptions';
import propTypes from 'prop-types';


class Performance extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dialogue: false,
            dialogueOver: false,
            firstDialogue: [
                "Alriiiiight! I knew you'd be a pro.",
                "Just let us know what kind of song you wanna play and we'll follow along.",
                "If you get overwhelmed, we can take a break. Our fans are chill like that.",
                "Let's rock!"
            ],
            songPlaying: false
        }
    }

    componentDidMount = () => {
        if (this.props.isFirst) {
            document.getElementById("cheer").play();
        }
    }

    buttonToggle = () => {
        this.props.performanceToggle();
        this.songOff();
    }

    toggleDialogue = () => {
        this.setState({
            dialogue: true
        })
    }

    dialogueEnd = () => {
        this.setState({
            dialogueOver: true
        })
        this.props.optionsOn();
        this.props.firstOff();
    }

    completeSongFull = (e) => {
        this.songOffCheer();
        this.props.completeSong(e);
    }

    songOn = () => {
        this.setState((state, props) => ({
            songPlaying: true
        }));
            document.getElementById("cheer").pause();

    }

    songOff = () => {
        this.setState({
            songPlaying: false
        })
    }

    songOffCheer = () => {
        document.getElementById("cheer").currentTime = 0;
            document.getElementById("cheer").play();
        this.setState({
            songPlaying: false
        })
      }

    render() {
        return (
            <div id="performance">
                <audio src={cheer} alt="audience cheering" id="cheer"></audio>
                {!this.props.isFirst &&
                    <button type="button" id="return" onClick={this.props.performanceToggle}>Back to Practice</button>}
                {this.props.isFirst && !this.state.dialogue &&
                    <Bubbles toggleDialogue={this.toggleDialogue} />}
                <div id="performance-interact">
                    {this.state.dialogue && !this.state.dialogueOver &&
                        <PerDialogue dialogue={this.state.firstDialogue} dialogueEnd={this.dialogueEnd} />}
                    {this.props.perOptions &&
                        <SongOptions incompleteSongs={this.props.incompleteSongs} completeSong={this.completeSongFull} songPlaying={this.state.songPlaying} songToggle={this.songOn} />}
                </div>
                <DrumMachine isPerformance={true} recordedSounds={this.props.recordedSounds} initialReecordTime={this.props.initialReecordTime} startRecord={this.props.startRecord} emptyRecord={this.props.emptyRecord} addRecordSound={this.props.addRecordSound}/>
            </div>
        )
    }
}

Performance.propTypes = {performanceToggle: propTypes.func.isRequired, isFirst: propTypes.bool.isRequired, firstOff: propTypes.func.isRequired, optionsOn: propTypes.func.isRequired, perOptions: propTypes.bool.isRequired, recordedSounds: propTypes.array.isRequired, initialRecordTime: propTypes.number.isRequired, startRecord: propTypes.func.isRequired, emptyRecord: propTypes.func.isRequired, addRecordSound: propTypes.func.isRequired, incompleteSongs: propTypes.array.isRequired, completeSong: propTypes.func.isRequired};

export default Performance

