import React, { Component } from 'react';
import DrumMachine from './DrumMachine';
import propTypes from 'prop-types';


class MainScreen extends Component {
    render() {
        return (
            <div id="main-screen">
                <DrumMachine recordedSounds={this.props.recordedSounds} initialRecordTime={this.props.initialRecordTime} startRecord={this.props.startRecord} emptyRecord={this.props.emptyRecord} addRecordSound={this.props.addRecordSound}/>
                <button id="ready" type="button" onClick={this.props.performanceToggle}>Ready to Rock!</button>
            </div>
        )
    }
}

MainScreen.propTypes = {performanceToggle: propTypes.func.isRequired,  recordedSounds: propTypes.array.isRequired, initialRecordTime: propTypes.number.isRequired, startRecord: propTypes.func.isRequired, emptyRecord: propTypes.func.isRequired, addRecordSound: propTypes.func.isRequired};

export default MainScreen;
