import React, { Component } from 'react';
import propTypes from 'prop-types';


class Bubbles extends Component {


    render() {
        return (
            <div id="bubbles">
                <div id="bubble1"></div>
                <div id="bubble2"></div>
                <div id="bubble3" onAnimationEnd={this.props.toggleDialogue}></div>
            </div>
        )
    }
}

Bubbles.propTypes = {toggleDialogue: propTypes.func.isRequired};

export default Bubbles
