import React, { Component } from 'react';
import img1 from '../static assets/standard floyd.png';
import img2 from '../static assets/heh heh floyd.png';
import img3 from '../static assets/disappointed floyd.png';
import img4 from '../static assets/razzed floyd.png';
import img5 from '../static assets/thumbs up.png';
import img6 from '../static assets/winky floyd.png';
import propTypes from 'prop-types';


class Floyd extends Component {
    render() {
        const floydArr = [img1, img2, img3, img4, img1, img5, img6];
        const floydNumber = this.props.currentLine;
        return (
            <div id="floyd-box">
                <img src={floydNumber < 7 ? floydArr[floydNumber] : img6} alt="Floyd the piano man" id="floyd-img" className="responsive-img"></img>
            </div>
        )
    }
}

Floyd.propTypes = {currentLine: propTypes.number.isRequired};

export default Floyd;
