import React, { Component } from 'react';
import propTypes from 'prop-types';


class IntDialogue extends Component {
    constructor(props) {
        super(props);
        this.state = {
            lines: 0    
    }
}

    dialClick = () => {
        let lineN = this.state.lines;
        lineN++;
        this.setState({
            lines: lineN
        });
    }

    render() {
        const dialogue = this.props.dialogue;
        const lines = this.state.lines;
        const screen = window.innerWidth;
        return (
            <div id="intro-interact">
            <div id="intro-dialogue" onClick={this.dialClick}>
                {screen > 800  
                ? dialogue.map((elem, index) => {
                    if(index <= lines) {
                        return <p className="intro-line" key={index}>{elem}</p>
                    } else {
                        return null;
                    }
                })
                : <p className="intro-line">{lines <= 6 ? dialogue[lines] : dialogue[6]}</p>}
                {lines < 6 && <i className="fas fa-caret-down pulsing"></i>}
            </div>
                <button id="start" className={lines < 6 ? "hidden btn btn-block col-xs-6" : "btn col-xs-6"} onClick={this.props.introClick}>Start</button>
            
            </div>
        )
    }
}

IntDialogue.propTypes = {dialogue: propTypes.array.isRequired, lines: propTypes.number.isRequired, introClick: propTypes.func.isRequired};
export default IntDialogue
