import React, { Component } from 'react';
import propTypes from 'prop-types';

class OtherPanel extends Component {
    
    handleClick = (event) => {
        const obj = this.props.sounds;
        const sound = obj.filter((elem) => {
            return elem.name === event.target.id;
        });
        if (this.props.isRecording && sound.length > 0) {
            document.getElementById(sound[0].button).currentTime = 0;
            document.getElementById(sound[0].button).play();
            this.props.current(sound[0].display);
            this.props.addRecordSound(sound[0]);
        } else {        
            document.getElementById(sound[0].button).currentTime = 0;
            document.getElementById(sound[0].button).play();
            this.props.current(sound[0].display);
    }}
    
    componentDidMount = () => {
        const sounds = this.props.sounds;
        sounds.map((elem) => {
            return document.getElementById(elem.name).addEventListener("click", this.handleClick)  
        });
    }

    render() {
        const sounds = this.props.sounds;
        return (
            <div id="other-panel">
                {sounds.map((elem) => {
                    return <button type="button" className="other-pad" id={elem.name} key={elem.button}>{elem.button}<audio src={elem.source} id={elem.button}></audio></button>
                })}
                <i className="fas fa-robot" id="other-icon"></i>
            </div>
        )
    }
}

OtherPanel.propTypes = {current: propTypes.func.isRequired, sounds: propTypes.array.isRequired, isRecording: propTypes.bool.isRequired, addRecordSound: propTypes.func.isRequired};

export default OtherPanel;
