import React from 'react';
import '../index.css';
import Intro from './Intro';
import MainScreen from './MainScreen';
import Performance from './Performance';
import Final from './Final';
import chaotic from '../sounds/A Chaotic Song.wav';
import dancing from '../sounds/A Dancing Song.wav';
import futuristic from '../sounds/A Futuristic Song.wav';
import energetic from '../sounds/An Energetic Song.wav';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isIntro: true,
      initialRecordTime: 0,
      recordedSounds: [],
      isPerformance: false,
      isFirst: true,
      perOptions: false,
      incompleteSongs: [
        {name: "A Chaotic Song", link: chaotic, id: 1},
        {name: "A Dancing Song", link: dancing, id: 2},
        {name: "A Futuristic Song", link: futuristic, id: 3},
        {name: "An Energetic Song", link: energetic, id: 4 }
    ],
    completedSongs: 0,
      final: false
    }
  }

  introClick = () => {
    this.setState({
      isIntro: false
    })
  }

  performanceToggle = () => {
    this.setState((state, props) => ({
      isPerformance: !state.isPerformance
    }));
    const audios = document.getElementsByTagName("audio");
    for (let i = 0; i < audios.length; i++) {
      audios[i].pause();
    }  
  }
  
  firstOff = () => {
    this.setState({
        isFirst: false
    });
}

  optionsOn = () => {
    this.setState({
      perOptions: true
    })
  }
  
  makeFinal = () => {
    this.setState({
      final: true
    })
  }

  startRecord = () => {
    const start = new Date();
    this.setState({
      initialRecordTime: start
    });
  }

  emptyRecord = () => {
    this.setState({
      recordedSounds: []
    })
  }

  addRecordSound = (sound) => {
    const initial = this.state.initialRecordTime;
    const newTime = new Date();
    this.setState((state, props) => ({
        recordedSounds: [...state.recordedSounds, {
            time: newTime - initial,
            sound: sound.button
        }]
    }))
}


completeSong = (e) => {
  let inc = this.state.completedSongs;
  let newInc = inc + 1;
  if(newInc > 3)  {
    this.makeFinal();
  };
  const incompleteArr = this.state.incompleteSongs.filter(elem => {
      return elem.id - e.target.id !== 0
  });
  this.setState({
      incompleteSongs: incompleteArr,
      completedSongs: newInc
  });
  
}



  render(){
    let isFinal = false;
    if (this.state.completedSongs > 3) {
      isFinal = true
    } else {
      isFinal = false
    }

    return (
      <div id="drum-machine">
        {this.state.isIntro && !isFinal && <Intro introClick={this.introClick}/>}
        {!this.state.isIntro && !this.state.isPerformance && !isFinal && <MainScreen performanceToggle={this.performanceToggle} recordedSounds={this.state.recordedSounds} initialRecordTime={this.state.initialRecordTime} startRecord={this.startRecord} emptyRecord={this.emptyRecord} addRecordSound={this.addRecordSound} />}
        {!this.state.isIntro && this.state.isPerformance && !isFinal && <Performance performanceToggle={this.performanceToggle} isFirst={this.state.isFirst} firstOff={this.firstOff} optionsOn={this.optionsOn} perOptions={this.state.perOptions} recordedSounds={this.state.recordedSounds} initialRecordTime={this.state.initialRecordTime} startRecord={this.startRecord} emptyRecord={this.emptyRecord} addRecordSound={this.addRecordSound} incompleteSongs={this.state.incompleteSongs} completeSong={this.completeSong} />}
        {isFinal && <Final />}
      </div>
    );
  };
};

export default App;
