import React, { Component } from 'react';
import victory from '../sounds/victory.mp3';
import post from '../static assets/post show floyd.png';
import pensive from '../static assets/pensive floyd.png';
import bye from '../static assets/bye floyd.png';

class Final extends Component {
    constructor(props) {
        super(props);
        this.state = {
            finalLines: [
                "You rocked tonight. I'm really glad we found you.",
                "Did you have fun? Heh heh, it's not too bad a gig is it?",
                "Making music...is always a beautiful thing, no matter how it's done. Never forget that.",
                "Well, I'm headed out. I'll see you at our next gig, right?"
            ],
            creditHeaders: [
                "Created By",
                "Music By",
                "The End"
            ],
            creditLines: [
                "Austin Knauer",
                `Komiku/Monplaisir,
                Frankum & Frankumjay`,
                ""
            ],
            lineInc: 0,
            creditInc: 0
        }
    }

    lineInc = () => {
        let inc = this.state.lineInc;
        let newInc = inc + 1;
        this.setState({
            lineInc: newInc
        })

        if (newInc > 3) {
            this.victory();
        }
    }

    creditInc = () => {
        let inc = this.state.creditInc;
        inc++;
        this.setState({
            creditInc: inc
        })
    }

    victory = () => {
        document.getElementById("victory").currentTime= 0;
        document.getElementById("victory").muted = false;
    }

    render() {
        return (
            <div id="final-screen">
                <audio src={victory} id="victory" muted autoPlay></audio>
                <div id="final-dialogue" className={this.state.lineInc > 3 ? "hidden" : undefined}>
                    <p id={`final-line${this.state.lineInc}`} onAnimationEnd={this.lineInc}>{this.state.lineInc < 4 ? this.state.finalLines[this.state.lineInc] : ""}</p>
                </div>
                <img src={this.state.lineInc === 2 ? pensive : this.state.lineInc === 3 ? bye : post} id="final-img" alt="Floyd against wall" className={this.state.lineInc < 4 ? "final-img" : "hidden"}></img>
                <div id={`credits${this.state.creditInc}`} className={this.state.lineInc < 4 ? "hidden" : undefined} onAnimationEnd={this.creditInc}>
                    <h1 id="credit-header">{this.state.creditInc < 2 ? this.state.creditHeaders[this.state.creditInc] : this.state.creditHeaders[2]}</h1>
                    <p id="credit-line">{this.state.creditLines[this.state.creditInc]}</p>                
                </div>
            </div>
        )
    }
}

export default Final
