import React, { Component } from 'react';
import propTypes from 'prop-types';


class PerDialogue extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dialogueNumber: 0
        }
    }


    dialogueInc = () => {
        let num = this.state.dialogueNumber;
        num++;
        this.setState({
            dialogueNumber: num
        })
    }

    render() {
        return (
            <div id="performance-dialogue" onClick={this.state.dialogueNumber < 3 ? this.dialogueInc : this.props.dialogueEnd}>
                <p id="performance-line">{this.props.dialogue[this.state.dialogueNumber]}</p>
                <i className="fas fa-guitar pulsing-dark"></i>
            </div>
            
        )
    }
}

PerDialogue.propTypes = {dialogue: propTypes.array.isRequired, dialogueEnd: propTypes.func.isRequired};

export default PerDialogue
