import React, { Component } from 'react';
import propTypes from 'prop-types';


class SongOptions extends Component {

    componentDidMount = () => {
        const buttons = this.props.incompleteSongs;
        buttons.map(elem => {
            return document.getElementById(elem.name).addEventListener("click", this.playSong)
        })
    }

    playSong = (e) => {
        const correct = this.props.incompleteSongs.filter(elem => {
            return elem.name === e.target.id
        });
        document.getElementById(correct[0].id).currentTime = 0;
        document.getElementById(correct[0].id).play();
        this.props.songToggle()
    }

    render() {
        return (
            <div id="song-options">
                            {this.props.incompleteSongs.map(elem => <button type="button" id={elem.name} key={elem.name} className={this.props.songPlaying ? "hidden" : "btn-outline song-button"}>{elem.name}
                            <audio src={elem.link} alt={elem.name} id={elem.id} onEnded={this.props.completeSong}></audio></button>)}
                        </div>
        )
    }
}

SongOptions.propTypes = {incompleteSongs: propTypes.array.isRequired, completeSong: propTypes.func.isRequired, songPlaying: propTypes.bool.isRequired, songToggle: propTypes.func.isRequired};


export default SongOptions
