import React, { Component } from 'react';
import DrumPanel from './DrumPanel';
import Center from './Center';
import OtherPanel from './OtherPanel';
import bassdrum from '../sounds/bass-drum.wav';
import crash from '../sounds/crash.wav';
import hihat from '../sounds/hi-hat.wav';
import hightom from '../sounds/high-tom.wav';
import lowtom from '../sounds/low-tom.wav';
import midtom from '../sounds/mid-tom.wav';
import ride from '../sounds/ride.wav';
import snare from '../sounds/snare.wav';
import sticks from '../sounds/sticks.wav';
import bass from '../sounds/bass.wav';
import beep from '../sounds/beep.wav';
import boing from '../sounds/boing.wav';
import chirp from '../sounds/chirp.ogg';
import chord from '../sounds/chord.wav';
import drop from '../sounds/drop.wav';
import glass from '../sounds/glass.wav';
import whoosh from '../sounds/whoosh.wav';
import woah from '../sounds/woah.wav';
import propTypes from 'prop-types';




class DrumMachine extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isRecording: false,
            isPlaying: false,
            timeOuts: [],
            currentSound: '',
            drumSounds: [
                { name: "Hi-hat", button: "Q", source: hihat, code: 81, display: "Hi-hat" },
                { name: "Crash", button: "W", source: crash, code: 87, display: "Crash Cymbal" },
                { name: "Ride", button: "E", source: ride, code: 69, display: "Ride Cymbal" },
                { name: "High-tom", button: "A", source: hightom, code: 65, display: "High-tom" },
                { name: "Mid-tom", button: "S", source: midtom, code: 83, display: "Mid-tom" },
                { name: "Low-tom", button: "D", source: lowtom, code: 68, display: "Low-tom" },
                { name: "Snare", button: "Z", source: snare, code: 90, display: "Snare Drum" },
                { name: "BassD", button: "X", source: bassdrum, code: 88, display: "Bass Drum" },
                { name: "Drumsticks", button: "C", source: sticks, code: 67, display: "Drum Sticks" },
            ],
            otherSounds: [
                { name: "BassG", button: "I", source: bass, code: 73, display: "Bass" },
                { name: "Beep", button: "O", source: beep, code: 79, display: "Beep" },
                { name: "Boing", button: "P", source: boing, code: 80, display: "Boing" },
                { name: "Chirp", button: "J", source: chirp, code: 74, display: "Chirp" },
                { name: "Chord", button: "K", source: chord, code: 75, display: "Chord" },
                { name: "Drop", button: "L", source: drop, code: 76, display: "Water Drop" },
                { name: "Glass", button: "B", source: glass, code: 66, display: "Glass" },
                { name: "Whoosh", button: "N", source: whoosh, code: 78, display: "Whoosh" },
                { name: "Woah", button: "M", source: woah, code: 77, display: "Woah" }
            ]
        }
    }

    timeOuts = [];

    handleKey = (event) => {
        const sounds = [...this.state.drumSounds, ...this.state.otherSounds];
        const key = event.which || event.keyCode;
        const sound = sounds.filter((elem) => {
            return elem.code === key;
        });
        if (this.state.isRecording && sound.length > 0) {
            document.getElementById(sound[0].button).currentTime = 0;
            document.getElementById(sound[0].button).play();
            this.currentSet(sound[0].display);
            this.props.addRecordSound(sound[0]);
        } else if (sound.length > 0) {
            document.getElementById(sound[0].button).currentTime = 0;
            document.getElementById(sound[0].button).play();
            this.currentSet(sound[0].display);
        } else {
            return null;
        }
    }

    componentDidMount = () => {
        document.addEventListener("keydown", this.handleKey);
    };

    componentWillUnmount = () => {
        document.removeEventListener("keydown", this.handleKey);
    }

    currentSet = (sound) => {
        this.setState({
            currentSound: sound
        })
    }

    clickRecord = () => {
        this.props.startRecord();
        this.props.emptyRecord();
        this.setState({
            isRecording: true,
            isPlaying: false
        });
    }

    stopRecord = () => {
        this.setState({
            isRecording: false
        })
    }
    
    handlePlay = (e) => {
        if (e.target.id === "play-button" || e.target.id === "triangle") {
            this.setState({
                isPlaying: true,
                isRecording: false
            });
            this.props.recordedSounds.forEach(elem => {
                this.timeOuts.push(setTimeout(function () {
                    document.getElementById(elem.sound).currentTime = 0;
                    document.getElementById(elem.sound).play();
                }, elem.time))
            });
        } else if (e.target.id === "stop-play" || e.target.id === "square") {
            this.setState({
                isPlaying: false,
            });
            this.timeOuts.forEach(elem => {
                clearTimeout(elem);
            });
            this.timeOuts = [];
        }
    }

    render() {
        return (

            <div id={this.props.isPerformance ? "machine-main-bottom" : "machine-main"}>
                <div id="title">
                    <h1>D</h1>
                    <h1>R</h1>
                    <h1>U</h1>
                    <h1>M</h1>
                    <h1>B</h1>
                    <h1>O</h1>
                    <h1>T</h1>
                    <h1>R</h1>
                    <h1>O</h1>
                    <h1>N</h1>
                    <h1>{" "}</h1>
                    <h1>4</h1>
                    <h1>0</h1>
                    <h1>0</h1>
                    <h1>0</h1>
                </div>
                <DrumPanel current={this.currentSet} sounds={this.state.drumSounds} isRecording={this.state.isRecording} addRecordSound={this.props.addRecordSound} />
                <Center current={this.state.currentSound} isRecording={this.state.isRecording} isPlaying={this.state.isPlaying} clickRecord={this.clickRecord} stopRecord={this.stopRecord} handlePlay={this.handlePlay} />
                <OtherPanel current={this.currentSet} sounds={this.state.otherSounds} isRecording={this.state.isRecording} addRecordSound={this.props.addRecordSound} />
            </div>

        )
    }
}

DrumMachine.propTypes = {isPerformance: propTypes.bool};

export default DrumMachine
