import React, { Component } from 'react';
import propTypes from 'prop-types';

class Center extends Component {
    render() {
        return (
            <div id="center-panel">
                <div id="display">
                    <p>{this.props.current}</p>
                </div>
                <div id="center-buttons">
                {!this.props.isRecording &&
                    <button id="record-button" type="button" className="center-button" onClick={this.props.clickRecord}><i className="fas fa-circle"></i></button>
                }
                {this.props.isRecording &&
                    <button id="stop-record" type="button" className="center-button" onClick={this.props.stopRecord}><i className="fas fa-stop"></i></button>
                }
                {!this.props.isPlaying &&
                    <button id="play-button" type="button" className="center-button" onClick={this.props.handlePlay}><i id="triangle" className="fas fa-play"></i></button>
                }
                {this.props.isPlaying &&
                    <button id="stop-play" type="button" className="center-button" onClick={this.props.handlePlay}><i id="square" className="fas fa-stop"></i></button>
                }
                </div>
            </div>
        )
    }
}

Center.propTypes = {current: propTypes.string.isRequired, isRecording: propTypes.bool.isRequired, isPlaying: propTypes.bool.isRequired, clickRecord: propTypes.func.isRequired, stopRecord: propTypes.func.isRequired, handlePlay: propTypes.func.isRequired}

export default Center;
