import React, { Component } from 'react';
import IntDialogue from './IntDialogue';
import Floyd from './Floyd';
import propTypes from 'prop-types';


class Intro extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dialogue: [
                "Oh, hey there. You made it. Thanks for coming.",
                "Our drummer split today. We were in kind of a pickle. You're a real life saver!",
                "Um, unfortunately he took his drum set with him...",
                "But we found this old machine in the green room! It's a little dusty, but it should do the trick.",
                "Our set is supposed to start in 15 minutes but we can go on whenever you're ready.",
                "Don't worry, I'm sure you'll get the hang of it.",
                "And hey. Welcome to the band." 
            ]
        }
    }

    render() {
        return (
            <div id="intro-screen">
                <button id="skip-int" className={"btn btn-lg"} onClick={this.props.introClick}>Skip &gt;&gt;</button>
                <IntDialogue dialogue={this.state.dialogue} lines={this.state.lines} introClick={this.props.introClick} />
                <Floyd currentLine={this.state.lines}/>
            </div>
        )
    }
}

Intro.propTypes = {introClick: propTypes.func.isRequired};
export default Intro;
